import SiHeader from "@/components/SimpleHeader";
import TabBar from "@/components/TabBar";
export default {
  name: "GroupPage",
  data() {
    return {
      ws: null,
      userMsgSend: "",
      msgList: [],
      userInfo: JSON.parse(localStorage.getItem("userInfo"))
    };
  },
  components: {
    SiHeader,
    TabBar
  },
  methods: {
    sendMsg() {
      if (this.userMsgSend) {
        this.ws.send(this.userMsgSend);
        this.userMsgSend = '';
      }
    }
  },
  beforeUnmount() {
    console.log('1111');
    this.ws.close();
  },
  mounted() {
    this.ws = new WebSocket(`${this.$config.socketUrl}/${this.userInfo.id}`);
    this.ws.onopen = function () {
      console.log("服务已连接");
    };
    this.ws.onmessage = msg => {
      console.log("来自服务器的数据：" + msg.data);
      let resData = JSON.parse(msg.data);
      let showObj = {};
      if (resData.type != 2) {
        showObj = {
          type: resData.type,
          id: resData.user.id + "_" + resData.type,
          msg: resData.msg + " " + resData.time
        };
        this.msgList = [...this.msgList, showObj];
      } else if (resData.type == 2) {
        showObj = {
          type: resData.type,
          id: resData.user.id,
          msg: resData.msg,
          displayName: resData.user.displayName || resData.user.name,
          headurl: resData.user.headurl
        };
        this.msgList = [...this.msgList, showObj];
      }
      this.$nextTick(() => {
        this.$refs.deviceScroll.scrollIntoView({
          block: "end",
          inline: "nearest"
        });
      });
    };
    this.ws.onclose = () => {
      console.log("服务器关闭");
    };
  }
};